<template>
    <div class="container">
        <h2 class="page-header">Install</h2>
        <p>use <strong>npm</strong> to install plugin</p>
        <pre class="brush:bash">
            npm i v-uploader --save
        </pre>
        <p>Include plugin in your <code>main.js</code> file</p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vUploader from 'v-uploader';

            // v-uploader plugin global config
            const uploaderConfig = () => {
                return {
                    // file uploader server url
                    uploadFileUrl: 'http://xxx/upload/publicFileUpload',
                    // file delete server url
                    deleteFileUrl: 'http://xxx/upload/deleteUploadFile',
                    // set the way to show upload message(upload fail message)
                    showMessage: (vue, message) => {
                        //using v-dialogs to show message
                        vue.$vDialog.alert(message, null, {messageType: 'error'});
                    }
                }
            };

            // install plugin with params
            Vue.use(vUploader, uploaderConfig);
        </pre>
        <p>there is using <a href="https://github.com/TerryZ/v-dialogs" target="_blank">v-dialogs</a> to show message in example</p>

        <h2 class="page-header">Deploy on your component</h2>
        <p>template code</p>
        <script type="syntaxhighlighter" class="brush:js">
        <![CDATA[
            <template>
              <!-- v-bind 'setting' data to config file uploader -->
              <!-- bind event 'done' to receive file uploaded info -->
              <v-uploader :setting="uploadConfig" @done="uploadDone" ></v-uploader>
            </template>
        ]]>
        </script>

        <p>script code</p>
        <pre class="brush:js">
        export default {
            data(){
                return {
                    uploadConfig: {
                        multiple: false,
                        language: 'en'//default: 'cn'
                    }
                }
            },
            methods:{
                //receive file uploaded info
                //@type Array
                uploadDone(files){
                    if(files && Array.isArray(files) && files.length){
                        // do something...
                    }
                }
            }
        };
        </pre>

        <h2 class="page-header">Example <small>the example will always return same image</small></h2>

        <h3>single file mode</h3>
        <v-uploader :setting="singleConfig" @done="uploadDone" ></v-uploader>
        <v-uploader :setting="singleConfig" @done="uploadDone" ></v-uploader>
        <v-uploader :setting="singleConfig" @done="uploadDone" ></v-uploader>


        <h3>multiple file mode <small>set option 'multiple' to <code>true</code></small></h3>
        <v-uploader :setting="multipleConfig" @done="uploadDone" ></v-uploader>


        <h2 class="page-header">Options</h2>
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <th>name</th>
                    <th>type</th>
                    <th>description</th>
                    <th>required</th>
                    <th>default</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>multiple</td>
                    <td>boolean</td>
                    <td>the file upload mode; in multiple upload mode, support drag files to upload</td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>language</td>
                    <td>string</td>
                    <td>
                        v-uploader support i18n and provide 2 language so far
                        <ul>
                            <li><code>'cn'</code></li>
                            <li><code>'en'</code></li>
                        </ul>
                    </td>
                    <td>false</td>
                    <td>'cn'</td>
                </tr>
                <tr>
                    <td>fileSizeLimit</td>
                    <td>string</td>
                    <td>upload file size limit</td>
                    <td>false</td>
                    <td>'5MB'</td>
                </tr>
                <tr>
                    <td>fileTypeExts</td>
                    <td>Array</td>
                    <td>set upload file extension restriction</td>
                    <td>false</td>
                    <td>['jpeg', 'jpg', 'gif', 'png']</td>
                </tr>
                <tr>
                    <td>imageMaxHeight</td>
                    <td>number</td>
                    <td>set image max height(pixel), set 0 to unlimit</td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>imageMaxWidth</td>
                    <td>number</td>
                    <td>set image max width(pixel), set 0 to unlimit</td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>imageMinHeight</td>
                    <td>number</td>
                    <td>set image min height(pixel), set 0 to unlimit</td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>imageMinWidth</td>
                    <td>number</td>
                    <td>set image min width(pixel), set 0 to unlimit</td>
                    <td>false</td>
                    <td>0</td>
                </tr>
            </tbody>
        </table>

        <h2 class="page-header">Events</h2>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th>name</th>
                <th>description</th>
                <th>arguments</th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>done</td>
                <td>return files info when uploaded success</td>
                <td>
                    <p>uploaded files info</p>
                    <p>type: Array</p>
                </td>
            </tr>
            </tbody>
        </table>

        <h2 class="page-header">Server side</h2>
        <h3>request</h3>

        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th>parameter name</th>
                <th>description</th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>uploadFileObj</td>
                <td>the name to receive file stream</td>
            </tr>
            <tr>
                <td>fileSizeLimit</td>
                <td>the setting of file size limit</td>
            </tr>
            <tr>
                <td>fileTypeExts</td>
                <td>the setting of file extensions</td>
            </tr>
            </tbody>
        </table>
        <h3>response</h3>
        <p>file upload and delete file just need return same format result(json)</p>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th>property name</th>
                <th>type</th>
                <th>description</th>
                <th>required</th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>success</td>
                <td>boolean</td>
                <td>file upload state</td>
                <td>true</td>
            </tr>
            <tr>
                <td>error</td>
                <td>string</td>
                <td>the reason of file upload fail</td>
                <td>true</td>
            </tr>
            <tr>
                <td>url</td>
                <td>string</td>
                <td>full path of image</td>
                <td>true</td>
            </tr>
            </tbody>
        </table>
        <h4>response example</h4>
        <pre class="brush:js">
        {
            "success": true,//required field
            "error": null,  //when file upload fail, this field will set the fail reason
            "url": "https://xx/someimg.jpg"//a full path of image to show in thumbnail
        }
        </pre>
    </div>
</template>

<script>
    export default {
        name: "Demo",
        data(){
            let arr = [];
            for (let i = 0; i < 108; i++) {
                arr.push(i+1);
            }
            return {
                singleConfig: {
                    language: 'en'
                },
                multipleConfig: {
                    multiple: true,
                    language: 'en'
                },
                singleCode: false,
                multipleCode: false
            };
        },
        methods: {
            uploadDone(files){
                if(files && Array.isArray(files) && files.length){
                    console.log(files);
                }
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style lang="scss" scoped>
</style>


